@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #141d26;
}

.swiper-container {
  width: 500px;
}

.swiper-wrapper {
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

.swiper-pagination {
  bottom: 0;
  padding-bottom: 10px;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.app-wrapper {
  color: white;
  height: 100vh;
  /* width: 80%; */
  margin: auto;
}

::selection {
  background: #add8e6; /* WebKit/Blink Browsers */
}

@media (max-width: 640px) {
  .swiper-container {
    width: 350px;
  }
}

/* @import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap"); */

h1 {
  font-family: "Press Start 2P", cursive;
  /* padding: 20px; */
  margin: 0;
}

.nav-wrapper {
  width: 90%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: auto;
}

.link-conatiner {
  display: flex;
  justify-content: space-evenly;
}

.link {
  font-size: 2em;
  font-family: "Press Start 2P", cursive;
  font-weight: bold;
  margin-left: 20px;
  margin-right: 20px;
  padding-bottom: 7px;
  border-bottom: 3px solid transparent;
}

.link:hover {
  border-bottom: 3px solid red;
}

@media (max-width: 800px) {
  .nav-wrapper {
    width: 95%;
  }
  .nav-wrapper h1 {
    padding: 0;
    margin-left: 0;
    font-size: 1.4em;
  }
  .link {
    font-size: 1.2em;
    margin-right: 0;
    margin-left: 15px;
  }
}

.header {
  font-family: "Ubuntu", sans-serif;
  margin: auto;
  text-align: center;
  width: 70%;
}

h1 {
  padding: 40px;
}

a {
  color: red;
  text-decoration: none;
}

p {
  padding-bottom: 30px;
}

@media (max-width: 800px) {
  h1 {
    font-size: 25px;
  }
}

.about-wrapper {
  width: 90%;
  text-align: center;
  align-items: center;
  margin: auto;
  font-family: "Ubuntu", sans-serif;
  line-height: 1.6;
}

.about-wrapper p {
  font-size: 1.8rem;
}

img {
  width: 350px;
}

@media (max-width: 800px) {
  .about-wrapper p {
    font-size: 1.4rem;
  }
}

